/* Background Image */
.welcome-container {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: url("https://img.freepik.com/premium-vector/health-care-abstract-geometric-medical-background-with-icons-concept-idea-healthcare-technology-innovation-medicine-healthcare-science-vector-design_258787-6248.jpg?w=740");
    background-size: cover;      /* Ensures the image covers the whole area */
    background-repeat: no-repeat; /* Prevents the image from repeating */
    background-position: center;  /* Centers the image */
    background-attachment: fixed;
    text-align: center;
    color: white;
    padding: 20px;
}

/* Drop-in Animation for "WELCOME" */
.welcome-header {
    margin-bottom: 50px;
}

.welcome-title {
    font-size: 700%;
    letter-spacing: 10px;
    font-weight: bold;
    color: #fff;
   
}

.welcome-title span {
    display: inline-block;
    opacity: 0;
    transform: translateY(-100px);
    animation: drop-in 0.5s ease-out forwards;
}

/* Keyframes for drop-in animation */
@keyframes drop-in {
    0% {
        opacity: 0;
        transform: translateY(-100px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

/* Individual letter delays */
.letter-0 { animation-delay: 0s; }
.letter-1 { animation-delay: 1.0s; }
.letter-2 { animation-delay: 2.0s; }
.letter-3 { animation-delay: 3.0s; }
.letter-4 { animation-delay: 4.0s; }
.letter-5 { animation-delay: 5.0s; }
.letter-6 { animation-delay: 6.0s; }

/* Button Styles */
.welcome-buttons {
    margin: 20px 0;

}

.btn-login, .btn-register {
    background-color: #3498db;
    color: #fff;
    padding: 20px 30px;
    margin: 30px;
    border: none;
    font-size: 250%;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease-in-out;
}

.btn-login:hover, .btn-register:hover {
    background-color: #2980b9;
}

/* Information List with Alternating Animations */
.welcome-info {
    margin-top: 10px;
}

.welcome-info h3 {
    margin-bottom: 20px;
    font-size: 2rem;
    color: #fff;
}

.welcome-info ul {
    list-style: none;
    padding: 0;
    font-size: 1.2rem;
}

.welcome-info li {
    margin: 20px 0;
    opacity: 0;
    transform: translateX(-100%);
    animation: slide-in 1.8s forwards;
}

@keyframes slide-in {
    0% {
        opacity: 0;
        transform: translateX(-100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

.info-left {
    animation: slide-in-left 0.8s ease-in-out forwards;
}

.info-right {
    animation: slide-in-right 0.8s ease-in-out forwards;
}

@keyframes slide-in-left {
    0% {
        opacity: 0;
        transform: translateX(-100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

@keyframes slide-in-right {
    0% {
        opacity: 0;
        transform: translateX(100%);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}

.info-left {
    animation-delay: 0.2s;
}

.info-right {
    animation-delay: 0.4s;
}

.info-left:nth-child(odd) {
    animation-delay: 0.6s;
}

.info-right:nth-child(even) {
    animation-delay: 0.8s;
}
