/* Background for the login page */
.login-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background-image: url('https://nordicapis.com/wp-content/uploads/How-to-Secure-API-Endpoints-768x480.jpg'); /* Replace with your background image */
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
    animation: fadeInBackground 2s ease-in-out; /* Fade in animation for background */
}

/* Animation for background */
@keyframes fadeInBackground {
    from {
        opacity: 0;
    }
    to {
        opacity: 1;
    }
}

/* Login form container styling */
.form-container {
    background-image: url('https://wallpapercave.com/wp/wp9764014.jpg');/* Semi-transparent white background */
    padding: 40px;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    text-align: center;
    width: 700px;
    opacity: 0;
    transform: translateY(100px); /* Slide from bottom */
    animation: slideUp 1.5s ease-out forwards; /* Animation */
    font-size: 200%;
}


/* Keyframes for sliding up and fading in the form */
@keyframes slideUp {
    from {
        opacity: 0;
        transform: translateY(100px); /* Starts off-screen */
    }
    to {
        opacity: 1;
        transform: translateY(0); /* End position */
    }
}

/* Input field styling */
.form-container input {
    width: 80%;
    padding: 10px;
    margin: 20px 0;
    border: 1px solid #ccc;
    border-radius: 5px;
    transition: all 0.3s ease-in-out; /* Add smooth transitions */
}

/* Input hover effect */
.form-container input:hover, 
.form-container input:focus {
    border-color: #007BFF;
    box-shadow: 0 0 8px rgba(0, 123, 255, 0.5); /* Highlight on hover/focus */
}

/* Button styling */
.form-container button {
    width: 83%;
    padding: 10px;
    background-color: #3676ba;
    font-size:100%;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s, transform 0.2s; /* Add transitions */
}

/* Button hover effect */
.form-container button:hover {
    background-color: #0056b3;
    transform: scale(1.05); /* Button grows slightly */
}

/* Error message styling */
.error {
    color: red;
    margin: 10px 0;
}

/* Sign-up link styling */
.signup-link {
    margin-top: 20px;
    font-size:100%;
}

.signup-link a {
    color: #574352;
    text-decoration: none;
    transition: color 0.3s; /* Add transition */
    
}

/* Link hover effect */
.signup-link a:hover {
    color: #0056b3;
    text-decoration: underline;
}
